import Floatmenu from '../comps/floatmenu'

function BrandPage() {
  return ( 
    <div className="brand">
      <Floatmenu 
        menu={["vision","brand_story","campaign","BI"]}
        handleClick={(idx) => {
          console.log(document.querySelector(`.brand img:nth-child(0)`))
      }}/>
      <section>
        <img src="../static/brand_vision.jpg"/>
      </section>
      <section>
        <img src="../static/brand_story.jpg"/>
      </section>
      <section>
        <img src="../static/brand_campaign.jpg"/>
      </section>
      <section>
        <img src="../static/brand_bi.jpg"/>
      </section>
      <style jsx>{`
        .brand img {
          vertical-align:top;
        }
      `}</style>
    </div>
  )
}

export default BrandPage